.container {
    padding: 30px 15px 0 15px;
    /*outline: 1px solid lightsalmon;*/
    background-color: #fafafa;
}

.img {
    border-radius: 22px;
    margin-right: 30px;
}

.card {
    display: flex;
    /*outline: 1px solid blue;*/
    margin-bottom: 50px;
}

.title {
    font-family: 'Raleway', sans-serif;
    font-size: 50px;
    font-weight: 800;
    margin-bottom: 20px;
}

.team {
    margin-bottom: 25px;
    font-family: 'Poppins', sans-serif;
    font-size: 24px;
    font-weight: 500;
}

.cardInfo {
    display: flex;
    font-family: 'Poppins', sans-serif;
    font-size: 12px;
    line-height: 1;
    color: #b2b3bd;
}
.cardInfo p:first-child {
    margin-right: 70px;
}

.editBlock {
    display: flex;
    margin-left: auto;
}

.editBlock ul li:not(:last-child){
    margin-bottom: 8px;
}
.editBlock ul{
    padding-left: 10px;
}
.editBlock ul li a img{
    margin: 0 auto 5px;
    text-align: center;
}

.editBlock ul li a {
    text-align: center;
    font-family: Poppins, sans-serif;
    font-size: 12px;
    line-height: 1;
    color: #212121;
}

.test {
    background-color: mediumseagreen;
    height: 2000px;
    width: 1600px;
    border: 5px solid red;
}
